

























































import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import { routerModule, repositoriesModule, authModule } from "@/store/modules";
import Timeline from "@/components/Timeline.vue";
import SnapshotsList from "@/components/repositories/SnapshotsList.vue";
import { ISnapshot, ISnapshotDetails } from "@/store/api";
import { parse } from "date-fns";
import { DateFormatter, FileIcons, LoadingScreen } from "@/mixins";
import * as path from "path-browserify";
import FolderSelectorDialog from "@/components/misc/FolderSelectorDialog.vue";
import SnapshotFileTree, {
  ISnapshotFileTreeFile,
  dedupeSelectedFiles
} from "@/components/misc/SnapshotFileTree.vue";
import * as api from "@/store/api";

@Component({
  components: {
    Timeline,
    SnapshotsList,
    FolderSelectorDialog,
    SnapshotFileTree
  }
})
export default class RepositorySnapshotsView extends Mixins(
  LoadingScreen,
  DateFormatter,
  FileIcons
) {
  restoreTargetSelectorOpen = false;
  restoreProgress = 100; // 0-100
  restorePath: string | null = null;

  get restoreRunning() {
    return this.restoreProgress < 100;
  }

  @Watch("restoreProgress")
  onProgressChange() {
    setTimeout(() => {
      requestAnimationFrame(() => {
        if (this.restoreRunning) {
          this.restoreProgress += Math.random() * 4;
          this.restoreProgress *= 1.1;
        } else if (this.restoreProgress !== 100) {
          this.restoreProgress = 100;
        }
      });
    }, Math.random() * 1000);
  }

  @Watch("restoreProgress")
  resetSelection() {
    if (!this.restoreRunning) {
      this.selectedFiles = [];
    }
  }

  restoreToFolder(restorePath: string) {
    this.restoreProgress = 1;
    this.restorePath = restorePath;
    api.restoreSnapshot(authModule.userId, {
      snapshotId: this.snapshotId,
      selectedPaths: this.dedupedSelectedFiles,
      restorePath
    });
  }

  get dedupedSelectedFiles() {
    return dedupeSelectedFiles(this.selectedFiles);
  }

  get repoId() {
    return Number(routerModule.params.repositoryId);
  }

  get snapshotId() {
    return routerModule.params.snapshotId;
  }

  created() {
    this.setLoading(true);
  }

  async mounted() {
    const response = await repositoriesModule.getSnapshotDetails(
      this.repoId,
      this.snapshotId
    );
    if (response.payload) {
      this.details = response.payload.details;
    }
    this.setLoading(false);
  }

  details: ISnapshotDetails | false = false;

  get repoName() {
    const repo = repositoriesModule.getById(this.repoId);
    return (repo && repo.name) || false;
  }

  selectedFiles: string[] = [];

  removeSubtree(file: string) {
    this.selectedFiles = this.selectedFiles.filter(
      f => !f.startsWith(file + "/") && f !== file
    );
  }
}
