
























import {
  Vue,
  Component,
  Prop,
  Mixins,
  Watch,
  Emit
} from "vue-property-decorator";
import { routerModule, repositoriesModule, authModule } from "@/store/modules";
import { FileIcons } from "@/mixins";
import * as path from "path-browserify";
import * as api from "@/store/api";
import equal from "deep-equal";
import debounce from "lodash.debounce";

export interface ISnapshotFileTreeFile {
  children?: ISnapshotFileTreeFile[];
  /**
   * Name displayed to the user
   */
  name: string;
  fullPath: string;
}

export const dedupeSelectedFiles = (selectedFiles: string[]) => {
  const deduped: { [idx: string]: true | undefined } = {};
  const f = [...selectedFiles].sort(
    (a, b) => a.split("/").length - b.split("/").length
  );

  for (let i = 0; i < f.length; ++i) {
    let dir = f[i];
    let parentDirAlreadySelected = false;
    for (;;) {
      dir = path.dirname(dir);
      if (dir === "/") break;
      if (deduped[dir]) {
        parentDirAlreadySelected = true;
        break;
      }
    }
    if (!parentDirAlreadySelected) {
      deduped[f[i]] = true;
    }
  }

  return Object.keys(deduped);
};

@Component({
  components: {}
})
export default class RepositorySnapshotsView extends Mixins(FileIcons) {
  @Prop({
    type: Array,
    required: true
  })
  selected!: string[];

  @Emit("update:selected")
  setSelected(selected: string[]) {}

  @Watch("selected")
  onUpdateSelected(cur: string[], prev: string[]) {
    if (!equal(cur, prev)) {
      this.selectedFiles = this.selected;
    }
  }

  @Watch("selectedFiles")
  onUpdateSelectedFiles(cur: string[], prev: string[]) {
    if (!equal(cur, prev)) {
      this.setSelected(cur);
    }
  }

  selectedFiles: string[] = [];

  @Prop({
    type: Array,
    required: true
  })
  filesInSnapshot!: ({ path: string })[];

  get snapshotTree() {
    if (!this.filesInSnapshot.length) return [];
    const tree: ISnapshotFileTreeFile[] = [];
    const parentNodes: ISnapshotFileTreeFile[][] = [tree];
    const files = this.filesInSnapshot;
    for (let i = 0; i < files.length; ++i) {
      const curFile = files[i];
      const nextFile = files[i + 1];
      const hasChildren = nextFile && nextFile.path.startsWith(curFile.path);
      // -1 for count of /, not substrings split by /; -1 for leading /
      const parentIndex = curFile.path.split("/").length - 2;
      const node: ISnapshotFileTreeFile = {
        name: path.basename(curFile.path),
        fullPath: curFile.path,
        children: hasChildren ? [] : undefined
      };
      if (hasChildren) {
        parentNodes[parentIndex + 1] = node.children!;
      }
      parentNodes[parentIndex].push(node);
    }
    return tree;
  }
}
